@import "./common.scss";
.gutenberghub-tabs-container {
	&.gutenberghub-tabs-frontend-container {
		.gutenberghub-tab-buttons-container {
			.gutenberghub-tab-button {
				cursor: pointer;
				.gutenberghub-tab-collapsible {
					.gutenberghub-tab-collapsible-inner-container {
						overflow: hidden;
					}
				}
			}
		}
		.gutenberghub-tab-contents-container {
			.gutenberghub-tab-content {
				display: none;
				&.gutenberghub-active-tab {
					display: block;
				}
			}
		}
	}
}
